import React from  "react";
import { Card,Form,Input ,Button,Radio ,message} from "antd"
import Qs from 'qs'
import axios from 'axios';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;

class FormChoice extends React.Component{
    state = {
        title: '',
        bodyA: '',
        bodyB:'',
        bodyC:'',
        bodyD:'',
        Unit:'',
        value:'2'
    }

    onTitleChange = (e) => {
        this.setState({
            title: e.target.value
        });
    };
    onAChange = (e) => {
        this.setState({
            bodyA: e.target.value
        });
    };
    onBChange = (e) => {
        this.setState({
            bodyB: e.target.value
        });
    };
    onCChange = (e) => {
        this.setState({
            bodyC: e.target.value
        });
    };
    onDChange = (e) => {
        this.setState({
            bodyD: e.target.value
        });
    };
    onUnitChange = (e) => {
        this.setState({
            Unit: e.target.value
        });
    };
    enterLoading = () => {
        let userInfo = this.props.form.getFieldsValue();
        this.props.form.validateFields((err,values)=>{
            if(!err){
                let data;
                    let response = axios.post(`http://localhost:8000/request/choice`,
                        Qs.stringify({
                        title :this.state.title,
                        bodyA: this.state.bodyA,
                        bodyB:this.state.bodyB,
                        bodyC:this.state.bodyC,
                        bodyD:this.state.bodyD,
                        Unit:this.state.Unit,
                        value:this.state.value,
                    }),
                    )
                        .then((response) => {
                        if (response.data == '114514') {
                            message.success('上传成功!')
                        } else {
                            message.error('上传失败!!')
                        }
                    })
            }
        })
    }

    closeLoading = () => {
        this.setState({ loading: false});
    }

    onChange = (e) => {
        console.log('radio checked', e.target.value);
        this.setState({
            value: e.target.value,
        });
    }

    render(){
        const { getFieldDecorator }  = this.props.form;

        return (
            <div>
                <Card title="选择题">
                    <Form >
                        <FormItem>
                            {
                                getFieldDecorator('question',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的题目"
                                        }
                                    ]
                                })(<Input placeholder="请输入题干"onChange={this.onTitleChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            {
                                getFieldDecorator('A',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的选项"
                                        }
                                    ]
                                })(<Input placeholder="请输入选项A" onChange={this.onAChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            {
                                getFieldDecorator('B',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的选项"
                                        }
                                    ]
                                })(<Input placeholder="请输入选项B" onChange={this.onBChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            {
                                getFieldDecorator('C',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的选项"
                                        }
                                    ]
                                })(<Input placeholder="请输入选项C" onChange={this.onCChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            {
                                getFieldDecorator('D',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的选项"
                                        }
                                    ]
                                })(<Input placeholder="请输入选项D" onChange={this.onDChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            {
                                getFieldDecorator('Unit',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的所属单元"
                                        }
                                    ]
                                })(<Input placeholder="请输入所属单元" onChange={this.onUnitChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            <p>正确答案：</p>
                            <RadioGroup onChange={this.onChange} value={this.state.value}>
                                <Radio value={1}>A</Radio>
                                <Radio value={2}>B</Radio>
                                <Radio value={3}>C</Radio>
                                <Radio value={4}>D</Radio>
                            </RadioGroup>
                            <br/>
                            <Button icon="upload" type="primary"  loading={this.state.loading} onClick={this.enterLoading} >上传</Button>
                        </FormItem>
                    </Form>
                </Card>
            </div>
        )
    }

}
export default Form.create()(FormChoice);
