import React from 'react'
import { Card , Button , Row ,Col ,message} from 'antd';
import Qs from 'qs'
import axios from 'axios';
import './history.css'
import {NavLink} from "react-router-dom";
import Util from "../../utils/utils";

export  default class History extends React.Component {
    state={
        time1:'',
        time2:'',
        time3:'',
    }
    componentWillMount(){
        let response = axios.post(`http://localhost:8000/time`,
            Qs.stringify({
                time:this.state.time1
            }),
        )
            .then((response) => {
                if (response.data != '') {
                    this.setState({
                        time1:response.data.time1,
                        time2:response.data.time2,
                        time3:response.data.time3,
                    })
                } else {
                    message.error('获取失败!!')
                }
            })
    }
    render() {
        return(
            <div className="homepage">
                <Card className="iquestion">
                    <Row>
                        <Col span="18">
                            <p>生成时间：{this.state.time1}</p>
                        </Col>
                        <Col span="3">
                            <div className="ibutton1">
                                <Button  icon="download" href="http://localhost:8000/paper/question1" >
                                        下载试卷
                                </Button>
                            </div>
                        </Col>
                        <Col span="3">
                            <div className="ibutton2">
                                <Button icon="download" href="http://localhost:8000/paper/answer1">
                                    下载答案
                                </Button>
                            </div>
                        </Col>
                    </Row>
                </Card>
                <br/>
                <Card className="iquestion">
                    <Row>
                        <Col span="18">
                            <p>生成时间：{this.state.time2}</p>
                        </Col>
                        <Col span="3">
                            <div className="ibutton1">
                                <Button  icon="download"  href="http://localhost:8000/paper/question2">
                                    下载试卷
                                </Button>
                            </div>
                        </Col>
                        <Col span="3">
                            <div className="ibutton2">
                                <Button icon="download" href="http://localhost:8000/paper/answer2">
                                    下载答案
                                </Button>
                            </div>
                        </Col>
                    </Row>
                </Card>
                <br/>
                <Card className="iquestion">
                    <Row>
                        <Col span="18">
                            <p>生成时间：{this.state.time3}</p>
                        </Col>
                        <Col span="3">
                            <div className="ibutton1">
                                <Button  icon="download"  href="http://localhost:8000/paper/question3">
                                    下载试卷
                                </Button>
                            </div>
                        </Col>
                        <Col span="3">
                            <div className="ibutton2">
                                <Button icon="download" href="http://localhost:8000/paper/answer3">
                                    下载答案
                                </Button>
                            </div>
                        </Col>
                    </Row>
                </Card>
            </div>
        )
    }

}