import React from 'react'
import { Button } from 'antd';
import { NavLink } from 'react-router-dom'
import './upload.css'

export  default class Upload extends React.Component {
    render() {
        return(
            <div className="homepage">
                <NavLink to='./display/choice'><Button> 选择题 </Button></NavLink>
                <NavLink to='./display/blank'><Button> 填空题 </Button></NavLink>
                <NavLink to='./display/calculation'><Button> 计算题 </Button></NavLink>
                <NavLink to='./display/shortanswer'><Button> 简答题 </Button></NavLink>
            </div>
        )
    }

}