import React from 'react'
import './index.css'
import { Rate } from 'antd';


 export default class Homepage extends React.Component{
     state = {
         value: 3,
     }
     handleChange = (value) => {
         this.setState({ value });
     }
    render(){
        const { value } = this.state;
        return (
            <div>
                <div className="homepage">
                    <h1>欢迎使用试卷自动生成及题库管理系统</h1>
                    <p1>本系统开发from：南京航空航天大学计算机科学与技术学院15级软件工程综合课程设计</p1>
                    <div>前端开发者：王令阳，裴天阳</div>
                    <div>后端开发者：谢云轩，王天行</div>
                    <div>算法支持：王天行</div>
                    <div>如果您在使用过程中出现问题可以随时联系我们</div>


                </div>

                <div className="grade">
                    <div>如果在使用过程中感觉不错可以为我们进行打分</div>
                        <span>
                            <Rate onChange={this.handleChange} value={value} />
                            {value && <span className="ant-rate-text">{value} stars</span>}
                        </span>
                    <div>十分感谢您的反馈</div>
            </div>
            </div>
        )
    }
 }