import React from 'react'
import {HashRouter , Route , Switch } from 'react-router-dom'
import Admin from './admin'
import App from "./App";
import Makepaper from './pages/paper/makepaper'
import History from  './pages/paper/history'
import Upload from './pages/question/upload'
import Manage from './pages/question/manage'
import Home from './pages/home'
import Question from './pages/question/questions'
import Choice from  './pages/question/display/choice'
import Blank from  './pages/question/display/blank'
import Caculate from  './pages/question/display/calculation'
import Shortanswer from './pages/question/display/shortanswer'
import MChoice from  './pages/question/manage/choice'
import MBlank from  './pages/question/manage/blank'
import MCaculate from  './pages/question/manage/calculation'
import MShortanswer from './pages/question/manage/shortanswer'


export default class IRouter extends React.Component{

    render(){
        return(
            <HashRouter>
                <App>
                    <Route path ="/admin" render={() =>
                        <Admin>
                            <Switch>
                                <Route exact={true} path="/admin" component={Home}/>
                                <Route exact={true} path="/admin/paper/makepaper" component={Makepaper}/>
                                <Route exact={true} path="/admin/paper/history" component={History}/>
                                <Route exact={true} path="/admin/question/upload" component={Upload}/>
                                <Route exact={true} path="/admin/question/manage" component={Manage}/>
                                <Route exact={true} path="/admin/question/questions" component={Question}/>
                                <Route exact={true} path="/admin/question/display/choice" component={Choice}/>
                                <Route exact={true} path="/admin/question/display/calculation" component={Caculate}/>
                                <Route exact={true} path="/admin/question/display/blank" component={Blank}/>
                                <Route exact={true} path="/admin/question/display/shortanswer" component={Shortanswer}/>
                                <Route exact={true} path="/admin/question/manage/choice" component={MChoice}/>
                                <Route exact={true} path="/admin/question/manage/calculation" component={MCaculate}/>
                                <Route exact={true} path="/admin/question/manage/blank" component={MBlank}/>
                                <Route exact={true} path="/admin/question/manage/shortanswer" component={MShortanswer}/>
                            </Switch>
                        </Admin>
                    }/>
                </App>
            </HashRouter>
        );
    }
}