import React from 'react'
import { Menu  } from 'antd';
import MenuConfig from './../../config/menuConfig'
import { NavLink } from 'react-router-dom'
import './index.css'
const SubMenu = Menu.SubMenu;
export  default  class Memu extends React.Component{
    componentWillMount(){
        const menuTreeNode = this.renderMenu(MenuConfig);
        this.setState({
            menuTreeNode
        })
    }
    //菜单渲染
    renderMenu =(data)=>{
        return data.map((item)=>{
            if(item.children){
                return (
                    <SubMenu title={item.tit1e} key={item.key} color="#ffffff">
                        {this.renderMenu(item.children)}
                    </SubMenu>
                )
            }
            return <Menu.Item title={item.tit1e} key={item.key}><NavLink to={item.key}>{item.tit1e}</NavLink></Menu.Item>
        })
    }

    render() {
        return (
            <div >
                <div className="logo">
                    {/*<img className="img" src="/assets/log.png" alt = ""/>*/}
                    <h1 className="h1">NUAA</h1>
                </div>
                <Menu  theme="dark">
                    {this.state.menuTreeNode}
                </Menu>
            </div>
        )
    }
}