import React from 'react'
import { DatePicker } from 'antd';
import { Card,Form,Input ,Button,Radio ,message} from "antd"
import Qs from 'qs'
import axios from 'axios';
import moment from 'moment';

import './makepaper.css'

const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const dateFormat = 'YYYY/MM/DD';


export  default class Makepaper extends React.Component {
    state = {
        loading: false,
        repetition:'0',
        subject:'数据结构',
        calculation:'1,2,3',
        shortanswer:'1,2,3',
        date:'2019/01/01',
        word:'',
    }



    onRepetitionChange = (e) => {
        this.setState({
            repetition: e.target.value
        });
    };
    onSubjectChange = (e) => {
        this.setState({
            subject: e.target.value
        });
    };
    onCalculationChange = (e) => {
        this.setState({
            calculation: e.target.value
        });
    };
    onShortanswerChange = (e) => {
        this.setState({
            shortanswer: e.target.value
        });
    };
    onDateChange = (e) => {
        this.setState({
            date: e.target.value
        });
    };
    enterLoading = () => {
        this.setState({ loading: true });
        let response = axios.post(`http://localhost:8000/request/make`,
            Qs.stringify({
                repetition:this.state.repetition,
                subject:this.state.subject,
                calculation:this.state.calculation,
                shortanswer:this.state.shortanswer,
                date:this.state.date,
            }),
        )
            .then((response) => {
                if (response.data == '114514') {
                    message.success('生成成功!')
                    this.setState({ loading: false});
                } else {
                    message.error('生成失败!!')
                    this.setState({ loading: false});
                }
            })
    }

    closeLoading = () => {
        this.setState({ loading: false});
    }



    render() {
        return(
            <div className="homepage">
                <div style={{ marginBottom: 16 }} className="input">
                    <Input addonBefore="重复率要求不超过" addonAfter="%" defaultValue={this.state.repetition} onChange={this.onRepetitionChange} />
                    <br></br>
                    <br></br>
                    <Input addonBefore="科目"  defaultValue={this.state.subject} onChange={this.onSubjectChange} />
                    <br></br>
                    <br></br>
                    <Input addonBefore="计算题单元考察" defaultValue={this.state.calculation} onChange={this.onCalculationChange} />
                    <br></br>
                    <br></br>
                    <Input addonBefore="简答题单元考察" defaultValue={this.state.shortanswer} onChange={this.onShortanswerChange}/>
                    <br></br>
                    <br></br>
                    <DatePicker defaultValue={moment('2019/01/01', dateFormat)} format={dateFormat} onChange={this.onDateChange} />
                </div>
                <Button type="primary"  onClick={this.enterLoading}>
                    生成试卷
                </Button>
                <Button icon="download"  href="http://localhost:8000/public/assets" >
                    下载试卷
                </Button>
                <Button icon="download" href="http://localhost:8000/public/answer">
                    下载答案
                </Button>
            </div>
        )
    }

}
