import React from  "react";
import { Card,Form,Input ,Button,Radio ,message} from "antd"
import Qs from 'qs'
import axios from 'axios';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;

class FormCalculate extends React.Component{
    state = {
        title: '',
        answer: '',
        Unit:'',
        Qid:'',
    }

    onTitleChange = (e) => {
        this.setState({
            title: e.target.value
        });
    };
    onQidChange = (e) => {
        this.setState({
            Qid: e.target.value
        });
    };
    onAnswerChange = (e) => {
        this.setState({
            answer: e.target.value
        });
    };
    onUnitChange = (e) => {
        this.setState({
            Unit: e.target.value
        });
    };

    enterLoading = () => {
        let userInfo = this.props.form.getFieldsValue();
        this.props.form.validateFields((err,values)=>{
            if(!err){
                let data;
                let response = axios.post(`http://localhost:8000/request/calculate`,
                    Qs.stringify({
                        title :this.state.title,
                        Unit:this.state.Unit,
                        answer:this.state.answer,
                        qid:this.state.Qid
                    }),
                )
                    .then((response) => {
                        if (response.data == '114514') {
                            message.success('上传成功!')
                        } else {
                            message.error('上传失败!!')
                        }
                    })
            }
        })
    }

    closeLoading = () => {
        this.setState({ loading: false});
    }


    render(){
        const { getFieldDecorator }  = this.props.form;

        return (
            <div>
                <Card title="计算题">
                    <Form >
                        <FormItem>
                            {
                                getFieldDecorator('question',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的题目"
                                        }
                                    ]
                                })(<Input placeholder="请输入题干"onChange={this.onTitleChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            {
                                getFieldDecorator('Unit',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的所属单元"
                                        }
                                    ]
                                })(<Input placeholder="请输入所属单元" onChange={this.onUnitChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            {
                                getFieldDecorator('Qid',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的题号"
                                        }
                                    ]
                                })(<Input placeholder="请输入题号" onChange={this.onQidChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            <p>正确答案：</p>
                        </FormItem>
                        <FormItem>
                            {
                                getFieldDecorator('Answer',{
                                    initialValue:'',
                                    rules:[
                                        {
                                            required:true,
                                            message:"不可以存在空的答案"
                                        }
                                    ]
                                })(<Input placeholder="请输入正确答案" onChange={this.onAnswerChange}></Input>)
                            }
                        </FormItem>
                        <FormItem>
                            <br/>
                            <Button icon="upload" type="primary"  loading={this.state.loading} onClick={this.enterLoading} >上传</Button>
                        </FormItem>
                    </Form>
                </Card>
            </div>
        )
    }

}
export default Form.create()(FormCalculate);